<template>
	<view class="flex flex-direction bg-white">
		<!-- 判断是否登录 -->
		<is-login currentPage="index" ></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">设备报修</block>
		</cu-custom>
		<luyj-search-bar cancelColor="#999999" radius="80" placeholder="请输入设备名称或SN码" @input="search_input" @cancel="search_cancel" :value="searchKey"></luyj-search-bar>
		<view class="cu-form-group margin-bottom-sm">
			<uni-data-picker placeholder="请选择产品分类" style="width: 50%;" :value="categoryId" :preload="true" :localdata="category_list"
				popup-title="产品分类" @nodeclick="changeCategory" ></uni-data-picker>
			<uni-data-picker  placeholder="请选择型号" style="width: 50%;"  :value="modelId" :preload="true" :localdata="model_list"
				popup-title="型号" @nodeclick="changeModel" ></uni-data-picker>
		</view>
		<uni-list>
			<!-- 设备列表 -->
			<uni-list-item v-for="(item , index ) in product_list" :key="index" showArrow :thumb="item.ImgUrl? item.ImgUrl : '/static/thumb/product_thumb.png'" thumbSize="base" to="/pages/repair-order/repair/repairsn?preview=true" >
				<template slot="body" class="flex flex-direction" style="width: 69%;">
					<view class=" cf text-cut">
						<view class="fl text-cut text-lg">
							<text class="text-bold">{{ item.Name }}</text>
						</view>
						<view class="text-sm text-grey fr cu-tag text-cut">{{ item.CategoryName }}</view>
					</view>
					<view class="text-sm text-cut text-grey">SN码：{{ item.ProductSn }}</view>
					<view class="text-sm text-cut text-grey">供应商：{{ item.SupName }}111111111</view>
					<view class="text-sm text-cut text-grey">所属门店：{{ item.MchName }}</view>
					<view class="text-sm text-cut text-grey">报修次数：0次</view>
				</template>
				<template slot="footer" class="padding-top-xl">
					<view class="cu-tag bg-cyan round margin-top margin-left paddding">报修</view>
				</template>
			</uni-list-item>
			<!-- 没有更多数据了 -->
			<uni-load-more status="noMore"></uni-load-more>
		</uni-list>
	</view>
</template>

<script>
	/**
	 * 设备主页
	 * @author 陆彦捷
	 * @date 2021.06.30 设备的主页样式
	 * @date 2021.07.01 设备的主页样式
	 * @date 2021.07.05 设备页样式修改
	 * @date 2021.07.23 重新引入login
	 */
	import organ from '@/common/storage/organstorage.js';		// 当前组织
	import categories from '@/common/data/category.js';	// 产品分类（模拟值）
	import models from '@/common/data/model.js';		// 型号（模拟值）
	import products from '@/common/data/product.js';	// 设备(模拟值)
	import changeobj from '@/common/function/changeobjectecordname.js';	// 格式化数组/对象
	
	var _self;
	export default {
		data() {
			return {
				// 样式参数
				preview : false,
				pattern: {
					color: '#7A7E83', // 文字默认颜色
					selectedColor: '#00BEB7',
					backgroundColor: '#fff',
					buttonColor: '#00BEB7'
				}, // 可选配置项样式（悬浮按钮）
				// 系统参数
				searchKey : '',		// 搜索条件
				CategoryId : 0 ,	// 设备分类id
				ModelId : 0 ,		// 型号id
				MchId: 0,			// 组织Id(参数的组织id或引入的组织id)
				Token:this.global.login.getToken(),	// Token值
				product_list:[],	// 设备列表
				category_list :[],	// 产品分类列表
				model_list :[],		// 产品型号列表
				categoryId : 0 ,	// 产品分类id(搜索条件)
				modelId : 0,		// 型号id(搜索条件)
				currentProductId : 0 ,// 当前选中的设备
				
			}
		},
		onLoad:function(options){
			_self = this;
			this.getParams(options);
			this.categories_request();			// 请求产品分类
			this.productlist_request();			// 设备请求(获取设备名称)
		},
		methods: {
			// ============================================ 初始化 ================================================
			/** 初始化参数
			 * @param {Object} options
			 */
			getParams : function(options){
				this.preview = options.preview != 'false' ? Boolean(options.preview): false;			// 是否返回上一页
				this.MchId = options.MchId ? options.MchId : organ.getIndustryId();	// 组织Id
				this.searchKey = options.searchKey ? options.searchKey : '';		// 搜索值
			},
			// ============================================ 监听事件 ===============================================
			/** 设备搜索事件
			 * @param {Object} e
			 */
			search_input : function(e){
				console.log('搜索设备事件',e);
			},
			/** 搜索设备取消事件
			 * @param {Object} e
			 */
			search_cancel : function(e){
				console.log("搜索设备取消事件",e);
			},
			/** 修改产品分类 (点击时修改)
			 * @param {Object} item
			 */
			changeCategory : function(item){
				this.categoryId = item.value;
				this.models_request(item.value);
			},
			/** 修改型号
			 * @param {Object} item
			 */
			changeModel : function(item){
				this.modelId = item.value;
			},
			// ============================================ 请求事件 ===============================================
			/**
			 * 产品分类列表请求(模拟)
			 */
			categories_request : function(){
				setTimeout(function(){
					_self.category_list= changeobj.change(categories.list, "categoryId", "name", "children");
					var temp = {
						"value": 0,
						"text": "无",
					};
					_self.category_list.unshift(temp);
				} , 3000);
			},
			/**
			 * 产品型号列表请求（模拟）
			 * @param {int} 产品分类 
			 */
			models_request : function(CategoryId){
				setTimeout(function(){
					if(CategoryId == 3){
						_self.model_list = changeobj.change(categories.list, "ModelId", "Name");
						var temp = {
							"value": 0,
							"text": "全部",
						};
						_self.model_list.unshift(temp)
					}else{
						_self.model_list = [{"value":0 , "text":"全部"}];
					}
				},3000);
			},
			/**
			 * 设备列表请求
			 */
			productlist_request : function(){
				uni.showLoading();
				setTimeout(function(){
					uni.hideLoading();
					_self.product_list = products.list;
				},500);
			},
			// =====================================================================================================
		}
	}
</script>

<style>

</style>
